<template>
  <div class="SharingBoxS">
    <!-- <el-container>
      <el-header height="23%" style="margin-bottom:30px">
        <grade />
      </el-header>
      <el-aside width="15%" style="display: inline-block;">
        <subject />
      </el-aside>
      <el-main class="main">
        <div class="homeWorkList"></div>
      </el-main>
    </el-container>-->
    <div class="container">
      <div class="heard" style="margin-bottom:30px">
        <!-- <grade /> -->
      </div>
      <div class="left">
        <subject class="subj" />
      </div>
      <div class="right">
        <div class="imgbox">
          <img src="../../../../static/images/微信图片_20191205174809.png" alt />
        </div>
        <div class="workList">
          <table>
            <thead>
              <tr style="background-color:#f8f8f8">
                <td style="color:#2ab8b3;font-size:16px;padding:5px">序号</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">作业名称</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">题型</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">难度</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">来源</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">时间</td>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item,index) in hwlist "
                :key="index"
                @click="chooseHomework(item)"
                class="trHover"
              >
                <td style="padding:10px">{{ index+1 }}</td>
                <td style="padding:10px;color:#f88869">内能</td>
                <td style="padding:10px">综合考试</td>
                <td style="padding:10px">中级</td>
                <td style="padding:10px">小陈老师</td>
                <td style="padding:10px">2019-12-6</td>
              </tr>
            </tbody>
          </table>
          <paginationVue class="page" />
        </div>
      </div>
    </div>
    <div class="takeWork">
      <div class="homeWork">
        <img src="../../../../static/images/作业状态.png" alt />
        <div class="NumBox">
          <span>已交：50人</span>
          <span>未交：10人</span>
        </div>
      </div>
      <div class="yijiao">
        <div class="takeName">已交作业的学生:</div>
        <div class="imgBox">
          <div class="ListBox" v-for="(item,index) in list " :key="index">
            <img src="../../../../static/images/erweima2.jpg" alt />
            <span>小陈</span>
            <span class="pigai">已批改</span>
          </div>
        </div>
      </div>
      <div class="yijiao">
        <div class="takeName">未交作业的学生:</div>
        <div class="imgBox">
          <div class="ListBox" v-for="(item,index) in list " :key="index">
            <img src="../../../../static/images/erweima2.jpg" alt />
            <span>小陈</span>
          </div>
        </div>
      </div>
    </div>
    <div class="workShow">
      <div class="workPigai">
        <img src="../../../../static/images/微信图片_20191205174834.png" alt />
      </div>
      <div class="pigaiBox">
        <img src="../../../../static/images/修改.jpg" alt />
      </div>
    </div>
  </div>
</template>

<script>
import grade from "@/components/grade/grade.vue";
import subject from "@/components/grade/subject.vue";
import Utils from "../../../../static/js/util";
import paginationVue from "../../../components/pagination/pagination.vue";

export default {
  components: {
    paginationVue,
    subject,
    grade,
    Utils
  },
  data() {
    return {
      list: [1, 1, 1, 1, 1, 1],
      hwlist: [1, 1, 1, 1, 1, 1, 1, 1],
      subjectId: "", //学科id
      textbookId: sessionStorage.getItem("textid"), //教科书id
      chapterId: "", //章id
      barId: "", //节id
      pageSize: 1,
      pageNum: 1,
      total: 0,
      value4: ""
    };
  },
  mounted() {},
  methods: {
    queryClassHomework() {
      Utils.$on("getData", res => {
        console.log(res);
      });
      Utils.$on("subjectId", res => {
        console.log(res, 222);
      });
      this.hwlist = [];
      // console.log()
      this.$post("/likework/queryClassHomework.do", {
        classId: this.value4, //班级id

        subjectId: this.subjectId, //学科id
        textbookId: "", //教科书id
        chapterId: this.chapterId, //章id
        barId: this.barId, //节id
        contentId: this.contentId, //内容id
        time: "",
        rows: 10,
        indexNo: this.pageNum,
        flag: 0 //发布状态 0-已发布 1-未发布
      }).then(response => {
        //console.log(response)
        if (response.code == "5000") {
          if (response.data.rows != "") {
            // console.log(response)
            this.hwlist = response.data.rows;
            this.total = response.data.pageCount;
          }
        }
      });
    }
  }
};
</script>

<style lang="less">
.SharingBoxS {
  .container {
    overflow: hidden;
    .left {
      width: 18%;
      height: 100%;
      display: inline-block;
      float: left;
      .subj {
        height: 100%;
      }
    }
    .right {
      background-color: #fff;
      display: inline-block;
      width: 81%;
      // border: 1px solid #bbb;
      overflow: hidden;
      float: right;
      .page {
        position: relative;
        right: -350px;
        bottom: 20px;
      }
      .imgbox {
        width: 100%;
        height: 62px;
        border-bottom: 1px solid #eee;
        img {
          margin-top: 17px;
        }
      }
      .workList {
        text-align: center;
        padding: 15px;
        table {
          width: 100%;
          .trHover:hover {
            background-color: #faf1ed;
          }
        }
      }
    }
  }
  .takeWork {
    border-top: 10px solid #f8f8f8;
    width: 100%;
    background-color: #fff;
    .homeWork {
      width: 100%;
      height: 56px;
      display: flex;
      justify-content: space-between;
      background-color: #faf1ed;
      img {
        width: 110px;
        height: 30px;
        margin-top: 13px;
      }
      .NumBox {
        flex: 1;
        text-align: center;
        line-height: 56px;
        span {
          font-size: 18px;
          margin-right: 10px;
        }
      }
    }
    .yijiao {
      width: 100%;
      height: 50%;
      border-bottom: 1px solid #f7f5f1;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;

      .takeName {
        width: 10%;
        text-align: center;
        border: 1px solid #f7f5f1;
        padding-top: 40px;
        font-size: 18px;
        color: #ff8500;
      }
      .imgBox {
        flex: 1;
        .pigai {
          display: block;
          width: 50px;
          margin: 0 auto;
          //    padding: 3px;
          border: 1px solid #2bb3ba;
          color: #2bb3ba;
        }
        .ListBox {
          width: 15%;
          padding: 8px;
          float: left;
          text-align: center;
          img {
            width: 60px;
            height: 60px;
            border-radius: 50%;
            display: block;
            margin: 0 auto;
            margin-bottom: 5px;
          }
        }
      }
    }
  }
  .workShow {
    background-color: #fff;
    .pigaiBox {
      width: 100%;
      img {
        margin-left: 165px;
      }
    }
    .workPigai {
      border-top: 10px solid #f8f8f8;
      height: 56px;
      width: 100%;
      img {
        margin-top: 13px;
      }
    }
  }
}
</style>


